html { 
  height: 100%;
  background: 
    linear-gradient(rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.8)),
    url(/static/media/USA_Counties.a53ac873.svg) no-repeat center center fixed;
  background-size: cover;
}

body {
  height: 100%;
  font-family: 'Roboto', sans-serif;
  padding: 0;
  margin: 0;
  /*display: flex;
  flex-direction: column;*/
}

body * { 
  box-sizing: border-box; 
  margin: 0;
  padding: 0;
}

/*************************************************************
/ REACT ROOT 
*************************************************************/
#root,
#root > div {
  height: 100%;
}

#root > div {
  display: flex;
  flex-direction: column;
}

/*************************************************************
/ HEADER
*************************************************************/
header {
  width: 100%;
  background: white;
  border-bottom: 2px solid royalblue;
}

.navbar-list {
  list-style-type: none;
  padding: 0;
  display: flex;
  align-items: center;
  color: royalblue;
  font-size: 0;
  max-width: 75rem;
  margin: 0 auto;
}

.navbar-list > li {
  font-size: 1rem;
  padding: 0.7rem 1rem;   
}

.navbar-list > li:first-child {
  margin-right: auto;
  font-size: 1.1rem;
  font-weight: 500;
}

.navbar-list a,
.navbar-list a:link,
.navbar-list a:visited,
.navbar-list a:hover,
.navbar-list a:active {
  text-decoration: none;
  color: royalblue;
}

/*************************************************************
/ MAIN 
*************************************************************/
main {
  min-height: 30rem;
  flex: 1 0 auto;
}

/*************************************************************
/ FOOTER
*************************************************************/
footer {
  width: 100%;
  border-top: 1px solid gray;
  background: white;
  text-align: center;
  padding: 20px 0;
}

footer p {
  font-size: 0.8rem;
  margin: 10px 0;
}

/*************************************************************
/ MEDIA QUERIES
*************************************************************/

/*up to large phone, small tablet*/
@media only screen and (max-width: 650px) {
  .navbar-list > li {
    padding: 0.7rem 0.5rem;   
  }
}

/* Search Form */

.search-form {
  width: 95%;
  max-width: 60rem;
  margin: 2rem auto 0;
  color: rgb(255, 50, 0);
  font-weight: 500;
  display: flex;
  flex-wrap: wrap;
}

.search-form fieldset {
  border: none;
  max-width: 40rem;
  padding: 0;
  margin: 0;
}

.search-form legend {
  font-size: 1.5rem;
  padding: 0.5rem 0;
}

.search-form ul {
  display: flex; 
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0;
  list-style-type: none;
}

.search-form li {
  margin-right: 1rem;
}

.search-form label {
  font-size: 1.2rem;
  display: block;
  max-width: 10rem;
  margin-bottom: 0.3rem;
}

.search-form input {
  font-size: 1rem;
  padding: 0.2rem;
  cursor: pointer;
  border: 1px solid gray;
  border-radius: 0.3rem;
  color: black;
  height: 2rem;
}

.search-form input:focus {
  border: 2px solid royalblue;
  border-radius: 0.3rem;
  outline: none;
}

.search-form .input-street {
  width: 14rem;
}

.search-form .input-city {
  width: 10rem;
}

.search-form .input-state,
.search-form .input-zip {
  width: 4rem;
}

.search-form button {
  background-color: royalblue;
  color: white;
  border: none;
  border-radius: 0.3rem;
  height: 2rem;
  font-size: 1rem;
  cursor: pointer;
  padding: 0 1rem;
  align-self: flex-end;
}

.search-form button:hover,
.search-form button:focus {
  background-color: navy;
  outline: none;
}

.search-form-errors {
  width: 95%;
  max-width: 60rem;
  margin: 1rem auto;
  color: red;
  list-style-type: none;
}

/* Search Results */

.results {
  width: 95%;
  max-width: 60rem;
  margin: 50px auto 0;
  list-style-type: none;
}

.results > li {
  background-color: white;
  border: 1px solid gray;
  box-shadow: 3px 3px gray;
  border-radius: 0.5rem;
  max-width: 60rem;
  margin: 1rem auto 2rem;
  padding: 0.5rem;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}

.input-and-rates-container {
  overflow: hidden;
}

.results-input {
  list-style-type: none;
  padding-left: 0;
  font-size: 1.1rem;
}

.results-input span {
  display: inline-block;
  width: 5rem;
}

.results-rates {
  list-style-type: none;
  padding: 0.5rem;
  margin-top: 1rem;
  border: 1px solid gray; 
  box-shadow: 2px 2px gray;
  border-radius: 0.3rem;
}

.results-rates > li:nth-of-type(n+2) {
  margin-top: 0.2rem;
}

.jurisdiction-list {
  list-style-type: none;
  padding-left: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.jurisdiction-list > li {
  width: 12rem;
}

.jurisdiction-level {
  max-width: 8rem;
}

.jurisdiction-name {
  max-width: 15rem;
} 

.jurisdiction-name input,
.jurisdiction-rate input {
  font-size: 0.9rem;
  padding: 0.2rem;
}

.jurisdiction-name input {
  width: 10rem;
}

.jurisdiction-rate input {
  width: 6rem;
}

.btn-copy {
  background-color: transparent;
  height: 1.3rem;
  width: 1.3rem;
  border: 1px solid gray;
  border-radius: 0.2rem;
  background-image: url(data:image/png;base64,);
  background-size: 1rem 1rem;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  position: relative;
}

.btn-copy:focus,
.btn-copy:hover {
  border: 2px solid royalblue;
  border-radius: 0.2rem;
  outline: none;
}

.btn-copy .tooltiptext {
  visibility: hidden;
  background-color: gray;
  color: white; 
  border-radius: 0.3rem;
  padding: 0.3rem;
  position: absolute; 
  z-index: 1;
  bottom: 100%;
  left: 50%;
}

.btn-copy:focus .tooltiptext,
.btn-copy:hover .tooltiptext {
  visibility: visible;
  border-radius: 0.2rem;
  outline: none;
}

.tooltiptext:focus,
.tooltiptext:hover {
  outline: none;
}

.no-rates-message {
  color: red;
  max-width: 30rem;
  margin-top: 20px;
}

.map-container {
  margin: 1rem 0;
}

.map {
  height: 20rem;
  max-height: 20rem;
  width: 20rem;
  max-width: 20rem;
  border: 1px solid gray;
  border-radius: 0.3rem;
  overflow: hidden;
}

.map img {
  width: 100%;
  height: 100%;
}

/*************************************************************
/ MEDIA QUERIES
*************************************************************/

/*small phone*/
@media only screen and (max-width: 321px) {
  .map {
    height: 18rem;
    max-height: 18rem;
    width: 18rem;
    max-width: 18rem;
  }
}

/*up to large phone, small tablet*/
@media only screen and (max-width: 650px) {
  .jurisdiction-list > li,
  .jurisdiction-level,
  .jurisdiction-name,
  .jurisdiction-rate {
    max-width: 100%;
    width: 100%;
  }

  .jurisdiction-name,
  .jurisdiction-rate {
    margin-top: 0.2rem;
  }

  .jurisdiction-name input,
  .jurisdiction-rate input {
    width: 12rem;
  }

  .results-rates > li:nth-of-type(n+2) .jurisdiction-level {
    margin-top: 1rem;
  }
}

/*up to a tablet*/
@media only screen and (max-width: 769px) {
  .search-form button {
    width: 100%;
    height: 3rem;
  }

  .search-form li {
    margin-right: 1rem;
  }

  .search-form input {
    margin-bottom: 0.5rem;
  }

  .map-container {
    margin: 1rem auto 2rem;
  }
}

#about {
  background-color: white;
  width: 95%;
  max-width: 60rem;
  margin: 50px auto;
  border: 1px solid gray;
  border-radius: 3px;
  box-shadow: 3px 3px gray;
  padding: 15px 50px;
  min-height: 800px;
}

#about h1 {
  color: royalblue;
}

#about p {
  margin: 25px 0;
  line-height: 1.5;
}

#about address p {
  margin: 15px 0;
  line-height: 1;
  font-style: normal;
}

#about figure {
  display: inline-block;
  border: 0.5px solid gray;
  border-radius: 1px;
  overflow: hidden;
  font-size: 0;
}

#about img {
  width: 200px;
}

/*************************************************************
/ MEDIA QUERIES
*************************************************************/

/*up to large phone, small tablet*/
@media only screen and (max-width: 650px) {
  #about {
    padding: 15px;
  }
}

#login {
  background-color: white;
  width: 95%;
  max-width: 30rem;
  margin: 50px auto;
  border: 1px solid gray;
  border-radius: 3px;
  box-shadow: 3px 3px gray;
  padding: 50px;
}

#login h1 {
  color: royalblue;
  text-align: center;
}

#login fieldset {
  border: none;
}

#login dt {
  margin: 20px 0 5px;
}

#login input {
  width: 100%;
  padding: 5px;
}

#login button {
  width: 100%;
  background-color: royalblue;
  color: white;
  border: none;
  border-radius: 0.3rem;
  padding: 15px;
  margin: 20px 0;
  cursor: pointer;
}

#login button:hover {
  background-color: navy;
  outline: none;
}

/*************************************************************
/ MEDIA QUERIES
*************************************************************/

/*up to large phone, small tablet*/
@media only screen and (max-width: 650px) {
  #login {
    padding: 50px 15px;
  }
}

#notfound {
  background-color: white;
  width: 95%;
  max-width: 60rem;
  margin: 50px auto;
  border: 1px solid gray;
  border-radius: 3px;
  box-shadow: 3px 3px gray;
  padding: 15px 50px;
  min-height: 800px;
}

#notfound h1 {
  color: royalblue;
}

#notfound p {
  margin: 25px 0;
  line-height: 1.5;
}

/*************************************************************
/ MEDIA QUERIES
*************************************************************/

/*up to large phone, small tablet*/
@media only screen and (max-width: 650px) {
  #notfound {
    padding: 15px;
  }
}

